// UI 
// 
export const SUPPORTED_LANGUAGES = {
  zh: {
    code: 'zh',
    name: 'Mandarin',
    nativeName: '',
    flag: ''
  },
  en: {
    code: 'en', 
    name: 'English',
    nativeName: 'English',
    flag: ''
  },
  ja: {
    code: 'ja',
    name: 'Japanese',
    nativeName: '',
    flag: ''
  },
  ko: {
    code: 'ko',
    name: 'Korean',
    nativeName: '',
    flag: ''
  }
};

// 
export const DEFAULT_LANGUAGE = 'en';

// localStorage 
export const STORAGE_KEY = 'lyra_exporter_language';

/**
 * 
 * @returns {string} 'zh'  'zh_'
 */
export const detectChineseVariant = () => {
  try {
    const browserLang = navigator.language || navigator.userLanguage || '';
    const lowerLang = browserLang.toLowerCase();
    
    // 
    if (lowerLang.includes('tw') ||    // 
        lowerLang.includes('hk') ||    // 
        lowerLang.includes('mo') ||    // 
        lowerLang.includes('hant')) {  // 
      return 'zh_';
    }
    
    // 
    return 'zh';
  } catch (error) {
    console.warn('Failed to detect Chinese variant:', error);
    return 'zh'; // 
  }
};

/**
 * 
 * zh-CN, zh-TW, zh-HK
 * @returns {string} 
 */
export const detectBrowserLanguage = () => {
  try {
    // 
    const browserLang = navigator.language || navigator.userLanguage || '';
    const lowerLang = browserLang.toLowerCase();
    
    // 
    if (SUPPORTED_LANGUAGES[browserLang]) {
      return browserLang;
    }
    
    //  -  'zh' detectChineseVariant 
    if (lowerLang.startsWith('zh')) {
      return 'zh';
    }
    
    //  en-US -> en, ja-JP -> ja
    const langPrefix = browserLang.split('-')[0];
    if (SUPPORTED_LANGUAGES[langPrefix]) {
      return langPrefix;
    }
    
    // 
    return DEFAULT_LANGUAGE;
  } catch (error) {
    console.warn('Failed to detect browser language:', error);
    return DEFAULT_LANGUAGE;
  }
};


/**
 * 
 * @param {Object} obj - 
 * @param {string} path -  'welcomePage.title'
 * @param {*} defaultValue - 
 * @returns {*} 
 */
export const getNestedValue = (obj, path, defaultValue = null) => {
  const keys = path.split('.');
  let current = obj;
  
  for (const key of keys) {
    if (current && typeof current === 'object' && key in current) {
      current = current[key];
    } else {
      return defaultValue;
    }
  }
  
  return current;
};

/**
 * 
 * @param {string} text - 
 * @param {Object} params - 
 * @returns {string} 
 */
export const interpolate = (text, params = {}) => {
  if (!text || typeof text !== 'string') {
    return text;
  }
  
  return text.replace(/\{\{(\w+)\}\}/g, (match, key) => {
    return key in params ? params[key] : match;
  });
};

/**
 * 
 * @param {string} languageCode - 
 * @returns {Promise<Object>} 
 */
export const loadLanguagePack = async (languageCode) => {
  try {
    // 
    let actualLanguageCode = languageCode;
    if (languageCode === 'zh') {
      actualLanguageCode = detectChineseVariant();
    }
    
    // 
    const module = await import(`./langs/${actualLanguageCode}.json`);
    return module.default || module;
  } catch (error) {
    console.warn(`Failed to load language pack for ${languageCode}:`, error);
    
    // 
    if (languageCode === 'zh') {
      try {
        const fallbackModule = await import(`./langs/zh.json`);
        return fallbackModule.default || fallbackModule;
      } catch (fallbackError) {
        console.error('Failed to load Chinese fallback:', fallbackError);
      }
    }
    
    // 
    if (languageCode === 'en') {
      return {};
    }
    
    // 
    try {
      const fallbackModule = await import(`./langs/${DEFAULT_LANGUAGE}.json`);
      return fallbackModule.default || fallbackModule;
    } catch (fallbackError) {
      console.error('Failed to load fallback language pack:', fallbackError);
      return {};
    }
  }
};

/**
 * 
 * localStorage >  > 
 * @returns {string} 
 */
export const getSavedLanguage = () => {
  try {
    const saved = localStorage.getItem(STORAGE_KEY);
    if (saved && SUPPORTED_LANGUAGES[saved]) {
      return saved;
    }
  } catch (error) {
    console.warn('Failed to read language from localStorage:', error);
  }
  
  // 
  return detectBrowserLanguage();
};

/**
 * 
 * @param {string} languageCode - 
 */
export const saveLanguage = (languageCode) => {
  try {
    localStorage.setItem(STORAGE_KEY, languageCode);
  } catch (error) {
    console.warn('Failed to save language to localStorage:', error);
  }
};

